/// <reference path="../definition/react.d.ts" />
/// <reference path="../definition/react-dom.d.ts" />
import React = require('react');
import ReactDOM = require('react-dom');

import w = require('../widget/widget');
import l = require('../widget/layout');

import likebutton = require('./likebutton');


export class App extends React.Component<any, any>{
    
    constructor(props: any) {
        super(props);
        this.state = { clickCount: 0 ,hidden: true};
    }

    handleClick() {
        let c = this.state.clickCount + 1;
        this.setState({ clickCount: c ,hidden:!this.state.hidden});
    }
    render() {
        return (
            <l.Vlayout hflex={1} vflex={1}>
                <button onClick={e => { this.handleClick() } }>Toggle visibility {this.state.clickCount}</button>
                <l.Hlayout hflex={1} vflex={1}>
                    <l.Vlayout hflex={1} style={{ background: 'yellow', padding: '2px' }}>
                        <l.Box>Top</l.Box>
                        <l.Box hidden={this.state.hidden} style={{ background: 'blue', padding: '2px' }}
                            animation={{ effect: w.AniEffect.fade, duration: 500 }}>AAAA</l.Box>
                        <l.Box>Bottom</l.Box>
                    </l.Vlayout>
                    <l.Vlayout hflex={1} style={{ background: 'yellow', padding: '2px' }}>
                        <l.Box>Top</l.Box>
                        <l.Box hidden={!this.state.hidden} style={{ background: 'blue', padding: '2px' }}
                            animation={{ effect: w.AniEffect.fade, duration: 500 }}>AAAA</l.Box>
                        <l.Box>Bottom</l.Box>
                    </l.Vlayout>
                    <l.Vlayout hflex={1} style={{ background: 'yellow', padding: '2px' }}>
                        <l.Box>Top</l.Box>
                        <l.Box hidden={this.state.hidden} style={{ background: 'green', padding: '2px' }}
                            animation={{ effect: w.AniEffect.slide, duration: 500 }}>BBB</l.Box>
                        <l.Box>Bottom</l.Box>
                    </l.Vlayout>
                    <l.Vlayout hflex={1} style={{ background: 'yellow', padding: '2px' }}>
                        <l.Box>Top</l.Box>
                        <l.Box hidden={!this.state.hidden} style={{ background: 'green', padding: '2px' }}
                            animation={{ effect: w.AniEffect.slide, duration: 500 }}>BBB</l.Box>
                        <l.Box>Bottom</l.Box>
                    </l.Vlayout>
                    <l.Vlayout hflex={1} style={{ background: 'yellow', padding: '2px' }}>
                        <l.Box>Top</l.Box>
                        <l.Box hidden={this.state.hidden} style={{ background: 'blue', padding: '2px' }}
                            animation={{ effect: w.AniEffect.fade, duration: 500 }}>AAAA</l.Box>
                        <l.Box hidden={this.state.hidden} style={{ background: 'green', padding: '2px' }}
                            animation={{ effect: w.AniEffect.slide, duration: 500 }}>BBB</l.Box>
                        <l.Box>Bottom</l.Box>
                    </l.Vlayout>
                    <l.Vlayout hflex={1} style={{ background: 'yellow', padding: '2px' }}>
                        <l.Box>Top</l.Box>
                        <l.Box hidden={!this.state.hidden} style={{ background: 'blue', padding: '2px' }}
                            animation={{ effect: w.AniEffect.fade, duration: 500 }}>AAAA</l.Box>
                        <l.Box hidden={!this.state.hidden} style={{ background: 'green', padding: '2px' }}
                            animation={{ effect: w.AniEffect.slide, duration: 500 }}>BBB</l.Box>
                        <l.Box>Bottom</l.Box>
                    </l.Vlayout>
                </l.Hlayout >
                <l.Vlayout hflex={1} vflex={1}>
                    <l.Hlayout >
                        <l.Box>Left</l.Box>
                        <l.Box hidden={!this.state.hidden} style={{ background: 'orange', padding: '2px' }}
                                animation={{ effect: w.AniEffect.slideLeft, duration: 500 }}>AAAA</l.Box>
                        <l.Box style={{ width:'300px', background: 'yellow', padding: '2px' }}>Right 1</l.Box>
                        <l.Box hflex={1} style={{ background: 'yellow', padding: '2px' , textAlign:'right'}}>Right 2</l.Box>
                    </l.Hlayout>
                    <l.Hlayout >
                        <l.Box>Left</l.Box>
                        <l.Box hidden={this.state.hidden} style={{ background: 'orange', padding: '2px' }}
                                animation={{ effect: w.AniEffect.slideLeft, duration: 500 }}>AAAA</l.Box>
                        <l.Box style={{ width:'300px', background: 'yellow', padding: '2px' }}>Right 1</l.Box>
                        <l.Box hflex={1} style={{ background: 'yellow', padding: '2px' , textAlign:'right'}}>Right 2</l.Box>
                    </l.Hlayout>
                </l.Vlayout>
            </l.Vlayout>
        )
    }
}

let content = document.getElementById('content');
if (content) {
    ReactDOM.render(
        <App/>
        ,
        document.getElementById('content')
    );
}